/* Book */

.books_search_tool{
    width: 100%;
}
.books_search{
    margin: 20px 20px 20px 20px;
    width: 60%;
    height: 50px;
    border: 0.5px solid grey;
    border-radius: 15px;
    display: inline-block;
}
.books_search input[type=text] {
    /* float: left; */
    padding: 6px;
    border: 1px;
    margin-top: 8px;
    margin-right: 16px;
    font-size: 17px;
    width: 60%;
}
.books_submit{
    background-color: palevioletred;
    border: none;
    color: white;
    padding: 6px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    /* margin: 20px 10px 0px 600px; */
    cursor: pointer;
    border-radius: 12px;
    display: inline-block; 
    width: 20%;
    height: 50px;
    
}

.books_content{
    width: 100%;
    height: 500px;
    margin: auto;
}
.books_sort{
    width: 100%;
    height: 100px;
    margin: 20px;
    position: relative;
    display: inline-block;
}
.books_dropbtn {
    background-color: #3498DB;
    color: white;
    padding: 16px;
    font-size: 16px;
    height: 50px;
    margin-right: 20px;
    border: none;
    cursor: pointer;
    /* float: right; */
    
}


.books_dropbtn:hover, .dropbtn:focus {
    background-color: #2980B9;
}

.books_myDropdown {
    display: none; 
    position: absolute;
    background-color:#f1f1f1;
    /* color: white; */
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    float: right;
}


.books_myDropdown a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}
.books_myDropdown a:hover {background-color: #ddd;}
.show {display: block;}
